<template>
  <div class="page-steps">
    <h1 class="page-title">Steps</h1>

    <h2 class="page-sub-title"></h2>
    <zan-steps :active="active" icon="certificate" icon-class="steps-success" title="" description="">
      <zan-step></zan-step>
      <zan-step></zan-step>
      <zan-step></zan-step>
      <zan-step></zan-step>
    </zan-steps>
    <zan-button @click="nextStep"></zan-button>

    <h2 class="page-sub-title"></h2>
    <zan-steps :active="active">
      <zan-step></zan-step>
      <zan-step></zan-step>
      <zan-step></zan-step>
      <zan-step></zan-step>
    </zan-steps>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0
    };
  },

  methods: {
    nextStep() {
      if (++this.active > 3) this.active = 0;
    }
  }
}
</script>

<style>
.steps-success {
  color: #06bf04;
}

.page-steps {
  .page-sub-title {
    padding: 20px 15px;
  }

  .zan-button {
    margin-left: 15px;
  }
}
</style>



