<template>
  <div class="page-dialog">
    <h1 class="page-title">Dialog</h1>

    <div class="zan-button-1">
      <zan-button @click="handleAlertClick">alert</zan-button>
    </div>

    <div class="zan-button-1">
      <zan-button @click="handleConfirmClick">confirm</zan-button>
    </div>

  </div>
</template>

<script>
import { Dialog } from 'src/index';

export default {
  methods: {
    handleAlertClick() {
      Dialog.alert({
        title: 'alert',
        message: '20PX20PX20PX20PX'
      }).then((action) => {
        console.log(action);
      });
    },

    handleConfirmClick() {
      Dialog.confirm({
        title: 'confirm',
        message: '20PX20PX20PX20PX'
      }).then((action) => {
        console.log(action);
      }, (error) => {
        console.log(error);
      });
    }
  }
};
</script>

<style>
@component-namespace page {
  @b dialog {
    padding: 0 15px;

    .zan-button-1 {
      margin-bottom: 10px;
    }
  }
}
</style>



